      size="large"
      type="primary"
      v-on:click="userLogin"
      v-bind:loading="isLoading"
    >进入</a-button>
  </div>
</template>
<script>
export default {
  name: "Login",
  data: function() {
    return {
      userName: "李逍遥",
      password: "",
      isLoading: false
    };
  },
  methods: {
    userNameEmpty: function() {
      this.userName = "";
    },
    passwordEmpty: function() {
      this.password = "";
    },
    userLogin: function() {
      this.isLoading = true;
      setTimeout(() => {
        this.$router.push({
          name: "dashboard"
        });
      }, 3000);
    }
  }
};
</script>
