<template>
  <div>
    <el-button type="primary" @click="onNewTask">新建观影</el-button>
    <el-dialog
      title="新建观影流程"
      :visible.sync="dialogFormVisible"
      width="40%"
      :before-close="handleClose"
    >
      <el-form ref="taskform" :model="form" label-width="80px">
        <el-form-item label="包间规格" :rules="[ { required: true, message: '包间规格不能为空'},]">
          <el-radio-group v-model="form.size">
            <el-radio label="168/大包间"></el-radio>
            <el-radio label="128/中包间"></el-radio>
            <el-radio label="98/小包间"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item
          label="观看时长"
          :rules="[
      { required: true, message: '观看时长不能为空'},
      { min:1, message: '观看时长必须为数字值'}
    ]"
        >
          <el-input-number v-model.number="form.hours" :min="0" @change="onHoursChange"></el-input-number>
        </el-form-item>
        <el-form-item label="小时单价" v-if="form.size">{{form.size}}</el-form-item>
        <el-form-item label="优惠金额" v-if="form.size">
          <el-input size="medium" v-model.number="form.free" type="number" placeholder="请输入内容"></el-input>
        </el-form-item>
        <el-form-item label="总金额" v-if="form.size" v-model="form.amount">{{getAllAmount}}</el-form-item>
        <el-form-item label="备注">
          <el-input
            size="medium"
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 4}"
            placeholder="请输入内容"
            v-model="form.note"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose">取 消</el-button>
        <el-button type="primary" @click="onSubmit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import LocalStorage from "../tool/tool.js";
import Audio from "../common/common";
import Push from "push.js";
import { fromInit } from "../data/dataInit.js";

export default {
  name: "newTask",
  data: function() {
    return {
      dialogFormVisible: false,
      form: new fromInit()
    };
  },
  computed: {
    getAllAmount: function() {
      this.form.amount =
        this.form.size.split("/")[0] * this.form.hours - this.form.free;
      return this.form.amount;
    }
  },
  methods: {
    onNewTask: function() {
      this.dialogFormVisible = true;
    },
    onSubmit: function() {
      // 开始校验表单
      if (this.form.size && this.form.hours) {
        this.form.begainDate = new Date();
        if (!isNaN(this.form.hours) && this.form.hours > 0) {
          // 开始计算时间
          this.form.begainDate = new Date();
          const endDateTime =
            this.form.begainDate.getTime() +
            this.form.hours * (1000 * 60 * 60) * 1;
          this.form.endDate = new Date(endDateTime);
          LocalStorage.set("list", [this.form]);
          this.modalClose();
          Audio.pushToast(true);
        }
      } else {
        this.$message({
          message: "请记得填写必填项哦~",
          type: "warning"
        });
      }
    },
    onHoursChange: function(value) {
      console.dir(this.form);
    },
    handleClose: function(e) {
      this.$confirm("关闭数据不会保存，确认关闭？")
        .then(_ => {
          this.modalClose();
        })
        .catch(_ => {});
    },
    modalClose: function() {
      this.form = new fromInit();
      this.dialogFormVisible = false;
    }
  }
};
</script>

