<script type="text/javascript">
import Push from "push.js";

// 定义一些公共的属性和方法
function play() {
  const audio = document.querySelectorAll("#bgAudio")[0];
  if (audio) {
    audio.play();
  }
}
function pause() {
  const audio = document.querySelectorAll("#bgAudio")[0];
  if (audio) {
    audio.load();
  }
}

function pushToast(isPlay) {
  // 请求浏览器系统通知
  if (isPlay) {
    play();
  }
  Push.create("小冇提醒您", {
    body: "有包间快到了！",
    timeout: 5000000000000,
    icon: "../../assets/logo.jpeg",
    onClick: function() {
      this.close();
      pause();
    },
    onClose: function() {
      pause();
    },
    onShow: function() {
      const _this = this;
    }
  });
}
// 暴露出这些属性和方法
export default {
  play,
  pause,
  pushToast
};
</script>